.App {
  color: #d6a838;
  font-weight: 200;
}

@media (min-width: 992px) {
  .container {
    max-width: 50rem !important;
  }
}

button {
  cursor: pointer;
}

.solid-rectangle-button {
  background-color: #d6a838;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
}

.balance-container {
  max-width: 320px;
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 768px){
  .balance-icon-container {
    margin-right: 1rem;
  }
  .balance-text-container {

  }
}


@media screen and (max-width: 767px){
  .balance-icon-container {
    float: left;
  }
  .balance-icon-container img {
    vertical-align: top;
  }

  .balance-text-container {
    float: right;
  }
}

.narrow-container {
  max-width: 720px;
  margin-left: auto;
  margin-right: auto;
}
