.doubleBorder {
  text-align: center;
  margin-top: auto;
  margin-bottom: auto;

  display: inline-block;
  padding: 0.5rem 1rem;
  color: #d6a838;
  position: relative;
  border: 1px solid #d6a838;
  background-color: transparent;
}

.doubleBorderTitle {
  margin-right: 1rem;
}

.doubleBorder span {
  position: absolute;
  background-color: #d6a838;
}

.doubleBorder span:nth-of-type(1) {
  right: -2px;
  top: 4px;
  width: 2px;
  height: 1px;
}

.doubleBorder span:nth-of-type(2) {
  right: -3px;
  top: 4px;
  width: 1px;
  height: 100%;
}

.doubleBorder span:nth-of-type(3) {
  right: -3px;
  bottom: -4px;
  width: 100%;
  height: 1px;
}

.doubleBorder span:nth-of-type(4) {
  left: 3px;
  bottom: -4px;
  width: 1px;
  height: 4px;
}
